import React from 'react';
import Tooltip from '@material-ui/core/Tooltip';
import '../assets/scss/index.scss';
import i18n from './../i18n';

const getConfig = (errors, touched) => {
  if (!touched) {
    return [];
  }

  

  if (errors && touched) {
    return ['fa-times-circle-o input-invalid', errors];
  }

  if (!errors && touched) {
    return ['fa-check-circle-o input-valid', i18n.t('valid-field')];
  }
};

const InputStatus = ({ errors, touched }) => {
  const [classes, extra] = getConfig(errors, touched);
  
  return (
    <Tooltip
      title={
        !Array.isArray(extra) ? (
          <span className="tooltip-message">
            {extra} <br />
          </span>
        ) : (
          extra.map(e => (
            <span className="tooltip-message">
              {e} <br />
            </span>
          ))
        )
      }
      placement="right">
      <span>
        <i className={'fa ' + classes} />
      </span>
    </Tooltip>
  );
};

export default InputStatus;