import React from 'react';
import { InputGroup, FormControl} from 'react-bootstrap';
import InputStatus from './InputStatus';

const CustomInput = ({ name,label, onChange , onBlur, value,type, isInvalid, errors,touched
  
}) => {
  
    
  return (
   <>
   {
       label === 'Phone' ? (
        <>   
        <label htmlFor="basic-url">{name} <InputStatus errors={errors} touched={touched}/></label>
        
       <InputGroup className="mb-3">
       <InputGroup.Prepend>
          <InputGroup.Text id="basic-addon1">+216</InputGroup.Text>
        </InputGroup.Prepend>
           <FormControl
               placeholder={label}
               aria-label={name}
               aria-describedby={name}
               onChange={onChange}
               onBlur={onBlur}
               value={value}
               isInvalid={isInvalid}
               label={label}
               name={name}
               type={type}
   
           />
       </InputGroup>
       </>
        ):(
            <>
            
            <label htmlFor="basic-url">{label}  <InputStatus errors={errors} touched={touched}/></label>

       <InputGroup className="mb-3">
   
           <FormControl
               placeholder={label}
               aria-label={label}
               aria-describedby={label}
               onChange={onChange}
               onBlur={onBlur}
               value={value}
               isInvalid={isInvalid}
               label={label}
               name={name}
               type={type}

               
   
           />
       </InputGroup>
       </>
        )
      
   }
    
    </>  
   
  );
};
export default CustomInput;